<template>
  <div class="my">
		<!--  -->
    <van-nav-bar title="" fixed>
			<!-- <van-icon name="info-o" slot="right" @click="showNewsP opup = true"/> -->
			<van-icon name="setting" slot="right" class="marl15" @click="showSetPopup = true"/>
		</van-nav-bar>
		<!--  -->
		<ul class="my-info" v-if="userData">
			<li class="fl head-image">
				<img :src="userData.ext_info.selfie" alt="" :onerror="defaultImg">
				<!-- <img v-else src="../assets/images/icon_avatar.png"/> -->
			</li>
			<li class="fl name-tel">
				<p class="name">{{userData.ext_info.name}}</p>
				<p class="tel">{{userData.cellphone}}</p>
			</li>  
			<!--  -->
			<li v-if="userData.ext_info.status == 1 && userData.type == 2" @click="cargoOwnerTypePopup = true " class="fr identification">{{userData.ext_info.status_desc}}<van-icon name="arrow"/></li>
			<li v-else @click="$router.push({path: '/my/user_detail'})" class="fr identification">{{userData.ext_info.status_desc}}<van-icon name="arrow"/></li>

		</ul>
		<!--  -->
		<div v-if="userData" class="transportation">
			<p class="title"></p>
			<van-tabbar v-model="active" :fixed="false" @change="onChange" class="transportation-tab">
				<van-tabbar-item v-if="userData.type == 2" icon="pending-orders" :info="userData.order.waiting_num" to="my/pending_order"></van-tabbar-item>

				<van-tabbar-item v-if="userData.type == 2" icon="pending-deliver" :info="userData.order.process_num" to="my/pending_deliver"></van-tabbar-item>
				<van-tabbar-item  v-if="userData.type == 1" icon="pending-deliver" :info="userData.order.process_num" to="my/car_pending_deliver"></van-tabbar-item>

				<van-tabbar-item icon="completed" :info="userData.order.complete_num" to="my/completed"></van-tabbar-item>

				<van-tabbar-item v-if="userData.type == 2" icon="tosend" :info="userData.order.my_recv_num" to="my/to_send"></van-tabbar-item>
			</van-tabbar>
		</div>
		<!--  -->
		<van-popup
			v-model="showSetPopup"
			position="right"
			:overlay="false"
			class="set-popup">
			<div class="setting-popup">
				<!--  -->
				<van-nav-bar
					title=""
					left-text=""
					left-arrow
					@click-left="showSetPopup = false"
				/>
				<!--  -->
				<div class="setting-content mart10">
					<van-cell-group>
						<van-cell title="" is-link @click="showPwdPopup = true"/>
						<van-cell title="" is-link @click="showTelPopup = true"/>
					</van-cell-group>
					<van-cell-group class="mart5">
						<van-cell title="" is-link @click="showAboutPopup = true"/>
					</van-cell-group>
					<div class="unlogin">
						<van-button size="large" type="" @click="unlogin"></van-button>
					</div>
				</div>
			</div>
		</van-popup>
		<!--  -->
		<van-popup
			v-model="showPwdPopup"
			position="right"
			:overlay="false"
			class="set-popup">
			<div class="setting-popup">
				<!--  -->
				<van-nav-bar
					title=""
					left-text=""
					left-arrow
					@click-left="showPwdPopup = false"
				/>
				<!--  -->
				<div class="set-pwd mart10">
					<van-cell-group>
						<van-field
							label=""
							type="password"
							v-model="passwordForm.password"
							placeholder=""
							@blur="inputBlur('password')"
							clearable/>
					</van-cell-group>
					<van-cell-group>
						<van-field
							label=""
							type="password"
							v-model="passwordForm.password_confirmation"
							placeholder=""
							@blur="inputBlur('password_confirmation')"
							clearable/>
					</van-cell-group>
					<div class="confirm-btn">
						<van-button size="large" type="primary" @click="savePassword"></van-button>
					</div>
				</div>
			</div>
		</van-popup>
		<!--  -->
		<van-popup
			v-model="showTelPopup"
			position="right"
			:overlay="false"
			class="set-popup">
			<div v-if="userData" class="setting-popup">
				<!--  -->
				<van-nav-bar
					title=""
					left-text=""
					left-arrow
					@click-left="showTelPopup = false"
				/>
				<!--  -->
				<div class="tel-bind mart10">
					<van-cell-group class="text-tel">
						<van-cell :value="` : ${userData.cellphone}`" />
					</van-cell-group>
					<van-cell-group>
						<van-field
							label=""
							v-model="phoneForm.cellphone"
							placeholder=""
							clearable/>
					</van-cell-group>
					<van-cell-group>
						<van-field
							v-model="phoneForm.verify_code"
							center
							clearable
							label=""
							placeholder=""
						>
							<!-- <van-button slot="button" size="small" @click="sendCode1" type="primary"></van-button> -->
							<van-button slot="button" :disabled="isDisabled" ref="validateBtn" class="button-validate" @click="sendCode1" size="small" type="primary">{{verificationCodePrompt}}</van-button>
						</van-field>
					</van-cell-group>
					<div class="modify-btn">
						<van-button size="large" type="primary" @click="updatePhone"></van-button>
					</div>
				</div>
			</div>
		</van-popup>
		<!--  -->
		<van-popup
			v-model="showAboutPopup"
			position="right"
			:overlay="false"
			class="set-popup">
			<div class="setting-popup">
				<!--  -->
				<van-nav-bar
					title=""
					left-text=""
					left-arrow
					@click-left="showAboutPopup = false"
				/>
				<!--  -->
				<div class="about-we mart15">
					<h3 class="title"></h3>
					<p class="content">
						
					</p>
					<p class="company"></p>
				</div>
			</div>
		</van-popup>
		<!--  -->
		<van-popup
			v-model="showNewsPopup"
			position="right"
			:overlay="false"
			class="set-popup">
			<div class="news-popup">
				<!--  -->
				<van-nav-bar
					title=""
					left-text=""
					left-arrow
					right-text=""
					@click-left="showNewsPopup = false"
					@click-right="$toast('')"
				/>
				<!--  -->
				<ul class="news-list">
					<li @click="showSysPopup = true">
						<img src="../assets/images/icon_message.png" alt="" class="fl">
						<p class="fl list-tit"></p>
					</li>
					<li @click="showNoticePopup = true">
						<img src="../assets/images/icon_message.png" alt="" class="fl">
						<p class="fl list-tit"></p>
					</li>
				</ul>
			</div>
		</van-popup>
		<!--  -->
		<van-popup
			v-model="showSysPopup"
			position="right"
			:overlay="false"
			class="set-popup">
			<div class="news-popup">
				<!--  -->
				<van-nav-bar
					title=""
					left-text=""
					left-arrow
					@click-left="showSysPopup = false"
				/>
				<!--  -->
				<div class="news-lists">
					<p class="date">2018-09-29 09:47:32</p>
					<p class="content"></p>
				</div>
				<div class="news-lists">
					<p class="date">2018-09-29 09:47:32</p>
					<p class="content"></p>
				</div>
			</div>
		</van-popup>
		<!--  -->
		<van-popup
			v-model="showNoticePopup"
			position="right"
			:overlay="false"
			class="set-popup">
			<div class="news-popup">
				<!--  -->
				<van-nav-bar
					title=""
					left-text=""
					left-arrow
					@click-left="showNoticePopup = false"
				/>
				<!--  -->
				<div class="no_resource">
					<img src="../assets/images/no_resource.jpg" alt="">
					<p class="tips">   </p>
				</div>
			</div>
		</van-popup>
		<!--  -->
		<van-popup 
			v-model="cargoOwnerTypePopup" 
			position="bottom" 
			:overlay="false"
			class="set-popup">
			 <!--  -->
			<van-picker  
				:show-toolbar="true" 
				:columns="cargoOwnerType" 
				@confirm="onCargoOwnerTypeChange" /> 
		</van-popup>
	</div>
</template>

<script>

	export default {
		data(){
			return{
				defaultImg: 'this.src="' + require('../assets/images/icon_avatar.png') + '"',
				userData:   null,
				active: 0,
				showSetPopup:false,
				showAboutPopup:false,
				showTelPopup:false,
				showPwdPopup:false,
				password:'',
				password_:'',
				showNewsPopup:false,
				showSysPopup:false,
				showNoticePopup:false, 
				cargoOwnerTypePopup: false, 
				passwordForm: { 
					password: null,
					password_confirmation: null,
				},
				phoneForm:{
					cellphone: null,
					use_type:  'rebind',
					verify_code: null,
				},
				verificationCodePrompt: '', //
				isDisabled:false, //truefalse
				cargoOwnerType: ['', ''], //
			};
		},
		mounted(){
			this.getUserInfo();
		},
		watch:{
			 
		},
		methods:{
			getUserInfo(){ 
				this.userData = null;
	  		this.axios.post(`${this.commonJs.basePath}/v1/users/info`, '').then( (response) => {
					this.userData = response.data.data;
					this.setCookie('userData', JSON.stringify(response.data.data));
	  		}).catch( (error) => {
	  			this.$toast(error)
	  		});
	  	},
			onChange(active){
			},
			/*
			 * 
			 */
			unlogin(){
				this.axios.get(`${this.commonJs.basePath}/v1/users/wxlogout`, '').then( (response) => {
					this.setCookie('userData','');
					this.setCookie('configMesg','');
					this.setCookie('carDetail','');
					this.$store.state.userData = null;
					
					this.commonJs.axiosHeaderData();
	  			this.$router.push({path: '/login'});
	  		}).catch( (error) => {
	  			this.$toast(error)
	  		});
			},
			inputBlur(val,val2,val3){
			},
			/*
			 * 
			 */
			savePassword(){
				if(this.validateForm('passwordForm')){
					this.axios.post(`${this.commonJs.basePath}/v1/users/change-password`, this.passwordForm).then( (response) => {
						this.$toast('');
						this.showPwdPopup = false;

		  		}).catch( (error) => {
		  			this.$toast(error)
		  		});
				}
			},
			/*
			 * 
			 */
			validateForm(formName){
				let bool = true, prompt = null;
				for(let key in this[formName]){
					if(this[formName][key]){
						if(this[formName][key].length < 6){
							prompt = '6';
							bool = false;
						}else if(key == 'password_confirmation'){
							if(this[formName].password_confirmation != this[formName].password){
								prompt = '';
								bool = false;
							}
						}
					}else {
						prompt = '';
						bool = false;
					}
				}
				if(!bool){
					this.$toast(prompt)
				}
				return bool;
			},
			/*
			 * 
			 */
			sendCode1() {
				this.$toast.loading('.......');
	      setTimeout(() => {
	      	this._obtainValidateCard();
	        let time = 60 ;
	        let timeSetInterval = setInterval(() => {
	        	time--;
	      		if(time == 0) {
							this.verificationCodePrompt = '';
							clearInterval(timeSetInterval);
							this.isDisabled = false;
	      		}else{
	      			this.verificationCodePrompt = time + '';
	      			this.isDisabled = true;
	      		}
	        },1000);
	        this.$toast.success('');
	      }, 1000);
	  	},
			/*
			 * 
			 */
			_obtainValidateCard(){
				this.axios.get(`${this.commonJs.basePath}/v1/verify-code/get`, {params: this.phoneForm}).then( (response) => {
	  		}).catch( (error) => {
	  			this.$toast(error)
	  		});
			},
			/*
			 * 
			 */
			updatePhone(){
				this.axios.post(`${this.commonJs.basePath}/v1/users/change-cellphone`, this.phoneForm).then( (response) => {
					this.showTelPopup = false;
	  		}).catch( (error) => {
	  			this.$toast(error)
	  		});
			}, 
			/*
			 * 
			 */
			onCargoOwnerTypeChange(val){  
				val == '' ?  this.$store.state.cargoOwnerType = 1 : this.$store.state.cargoOwnerType = 2;
				this.$router.push({'path': '/my/user_detail'});				
			} 
		},
	};
</script>

<style scoped>
	/* style */
	.my-info{
		overflow: hidden;
