    width: 100%;
	}
	.setting-popup .setting-content .unlogin button{
		color: #de2f2f;
	}
	/*  */
	.setting-popup .about-we{
		padding: 0px 5px;
	}
	.setting-popup .about-we .title{
		margin-bottom: 10px;
		text-align: center;
	}
	.setting-popup .about-we .content{
		margin-bottom: 10px;
    text-align: left;
    text-indent: 28px;
    color: #222;
    line-height: 24px;
	}
	.setting-popup .about-we .company{
		text-align: right;
    color: #222;
	}
	/*  */
	.setting-popup .tel-bind{
		background: #fff;
	}
	.setting-popup .tel-bind .text-tel .van-cell{
		color: #666;
	}
	.setting-popup .tel-bind .modify-btn{
		border-top: 40px solid #f7f7f7;
	}
	/*  */
	.setting-popup .set-pwd .confirm-btn{
		border-top: 40px solid #f7f7f7;
	}

	/* style */
	.news-popup{
		overflow: hidden;
	}
	.news-popup .news-list{
		width: 95%;
		margin: 10px auto 0;
		background: #fff;
		overflow: hidden;
		border-radius: 5px;
	}
	.news-popup .news-list li{
		border-bottom: 1px solid #eee;
		padding: 8px 12px;
		line-height: 40px;
		height: 40px;
	}
	.news-popup .news-list li img{
		height: 30px;
		margin-top: 5px;
	}
	.news-popup .news-list li .list-tit{
		margin-left: 10px;
	}
	/*  */
	.news-popup .news-lists{
		width: 95%;
		margin: 10px auto 0;
		background: #fff;
		overflow: hidden;
		border-radius: 5px;
		padding: 8px;
		text-align: left;
		box-sizing: border-box;
	}
	.news-popup .news-lists .date{
		color: #999;
		font-size: 12px;
	}
	.news-popup .news-lists .content{
		margin-top: 4px;
	}

</style>
