import vue from 'vue';
const Vue = new vue();
export default {
  basePath: `${window.location.origin}/api`,
  filePath: `http://up-z2.qiniu.com`,
  pagination:{     //elementUi
    pageDefault:1,        //
    prePageDefault: 50,   //
  }, 
  /*
  *  000000
  * @params {string/number} timeSetback 
  * @params {string} format 
  */
  _convertDate(time, format){
      return time ? new Date(time).Format(format) : '';
   },
  /*
   * 
   */
  axiosHeaderData(){
    // vueaxiosheaders
    Vue.axios.defaults.headers.common['app-version'] = ''; //
    Vue.axios.defaults.headers.common['model'] = '';//
    Vue.axios.defaults.headers.common['system-version'] = ''; //
    Vue.axios.defaults.headers.common['system-platform'] = ''; //  [iOS, Android]
    Vue.axios.defaults.headers.common['user-type'] = '';
    Vue.axios.defaults.headers.common['request-token'] = '';
    Vue.axios.defaults.headers.common['request-timestamp'] = '';
  },
  /*
     * headers
     */
  _getHeaders(data){
    let requestTimestamp = JSON.stringify(parseInt(new Date().getTime()/1000));
    Vue.axios.defaults.headers.common['app-version'] = '1.0'; //
    Vue.axios.defaults.headers.common['model'] = 'WX';//
    Vue.axios.defaults.headers.common['system-version'] = '6.0'; //
    Vue.axios.defaults.headers.common['system-platform'] = 'Android'; //  [iOS, Android]
    Vue.axios.defaults.headers.common['user-type'] = data.type;
    Vue.axios.defaults.headers.common['request-token'] = Vue.md5(Vue.md5(requestTimestamp).substring(0,10) + 'y-c-b-2-0-1-7');
    Vue.axios.defaults.headers.common['request-timestamp'] = requestTimestamp;
  },
  /*
   * Y
   */
  getScrollTop(){
var scrollTop = 0, bodyScrollTop = 0, documentScrollTop = 0;
if(document.body){
bodyScrollTop = document.body.scrollTop;
}
if(document.documentElement){
documentScrollTop = document.documentElement.scrollTop;
}
scrollTop = (bodyScrollTop - documentScrollTop > 0) ? bodyScrollTop : documentScrollTop;
return scrollTop;
   },
  /*
   * 
   */
  getScrollHeight(){
var scrollHeight = 0, bodyScrollHeight = 0, documentScrollHeight = 0;
if(document.body){
bodyScrollHeight = document.body.scrollHeight;
}
if(document.documentElement){
documentScrollHeight = document.documentElement.scrollHeight;
}
scrollHeight = (bodyScrollHeight - documentScrollHeight > 0) ? bodyScrollHeight : documentScrollHeight;
return scrollHeight;
   },
  /*
   * 
   */
  getWindowHeight(){
var windowHeight = 0;
if(document.compatMode == "CSS1Compat"){
windowHeight = document.documentElement.clientHeight;
}else{
windowHeight = document.body.clientHeight;
}
return windowHeight;
   },
}

/*
*   
*/
Date.prototype.Format = function (fmt) { 
  var o = {
      "M+": this.getMonth() + 1, // 
      "d+": this.getDate(), // 
      "h+": this.getHours(), // 
      "m+": this.getMinutes(), // 
      "s+": this.getSeconds(), // 
      "q+": Math.floor((this.getMonth() + 3) / 3), // 
      "S": this.getMilliseconds() // 
  };
  if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
  for (var k in o)
  if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
  return fmt;
}